import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
// 
export default new Vuex.Store({
	// 
  state: {
    // 
    home:true,
    // 
    selected:0,

    total:0,

    allM:[]
  },
  // 
  mutations: {
  	// 
    nohome(state,n){
      state.home=n;
    },
    imgUrl(state,n){
      state.allM.push(n);
    }
  }
})


